import { Injectable } from "@angular/core";
import { CommonService } from "./common/common.service";
import { Observable } from "rxjs";
import { UrlService } from './url/url.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/internal/operators';
import { ApiService } from './api/api.service';

@Injectable({
  providedIn: "root"
})
export class PrizeService {
  BASE_URL: any = this.url.SERVER_URL + '/api/';
  apiEndPoints:any;
  constructor(private comm: CommonService,
    private url:UrlService,
    private http: HttpClient,
    private api:ApiService) {
    {
      this.apiEndPoints = {
        //Prize
        addPrize: 'admin/addPrize',
        prizeListing:'admin/prizeListing',
        deletePrize:'admin/deletePrize'
      }
      for (let key in this.apiEndPoints) {
        this.apiEndPoints[key] = this.BASE_URL + this.apiEndPoints[key];
      }
  }
}
protected getHeaders(): { headers: HttpHeaders } {
   return {
     headers: new HttpHeaders({
      'Accept': 'application/json',
       'Authorization': 'Bearer '+this.api.getToken()
     })
   };
 }
 prizeListing(){
  return this.http
  .get<any>(this.apiEndPoints.prizeListing,this.getHeaders())
  .pipe(
    catchError(this.handleError<any>('productListing'))
  );
}
addPrize(prize: any): Observable<any> {
 const data:FormData=new FormData();
  data.append('name',prize.prizeName);
  data.append('image',prize.prizeImage);
  data.append('description',prize.prizeDescription);
prize.specifications.forEach((spec,i) => {
    data.append(`specifications[${i}][name]`,spec.name)
    data.append(`specifications[${i}][value]`,spec.value)
    data.append(`specifications[${i}][arabic_name]`,spec.arabicName)
    data.append(`specifications[${i}][arabic_value]`,spec.arabicValue)
});
   return this.http
   .post<any>(this.apiEndPoints.addPrize, data,this.getHeaders())
   .pipe(
     catchError(this.handleError<any>('Add Product'))
   );
 }
 
 deletePrize(id){
  const data:FormData=new FormData();
  data.append('id',id);
  return this.http
  .post<any>(this.apiEndPoints.deletePrize,data,this.getHeaders())
  .pipe(
    catchError(this.handleError<any>('Delete Prize'))
  );
 }
 private handleError<T>(operation = 'operation', result?: T) {
  return (error: any): Observable<T> => {
    // TODO: send the error to remote logging infrastructure
    console.error(error); // log to console instead
    return;
  };
}
}
