import { Component, OnInit } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl,
  FormArray
} from "@angular/forms";
import { Router } from '@angular/router';
import { PrizeService } from 'src/app/idealz/services/prize.service';
import { ToastrManager } from 'ng6-toastr-notifications';

@Component({
  selector: 'app-add-prize',
  templateUrl: './add-prize.component.html',
  styleUrls: ['./add-prize.component.scss']
})
export class AddPrizeComponent implements OnInit {
  productForm: FormGroup;
  prizeImage: any;
  submitted: boolean = false;
  history = window.history;
  constructor(private formBuilder: FormBuilder,
     private router: Router,
     private prizeService:PrizeService,
     private toastr:ToastrManager) { }

  ngOnInit() {
    this.productForm = this.formBuilder.group({
      prizeName: new FormControl("", [Validators.required, Validators.maxLength(20), Validators.pattern(".*\\S.*[a-zA-z0-9 ]")]),
      arabicPrizeName: new FormControl("", [Validators.required, Validators.maxLength(20), Validators.pattern(".*\\S.*[a-zA-z0-9 ]")]),
      prizeDescription: new FormControl(
        "",
        Validators.compose([Validators.required, Validators.maxLength(250)])
      ),
      arabicPrizeDescription: new FormControl(),
      specifications: this.formBuilder.array([this.formBuilder.group({ name: ['', Validators.required], value: ['', Validators.required], arabicName: ['', Validators.required], arabicValue: ['', Validators.required] })]),

      prizeImage: new FormControl("", [Validators.required])
    });
  }

  get specifications() {
    return this.productForm.get('specifications') as FormArray;
  }

  addSpecifications() {
    this.specifications.push(this.formBuilder.group({ name: ['', Validators.required], value: ['', Validators.required], arabicName: ['', Validators.required], arabicValue: ['', Validators.required] }));
  }

  deleteSpecification(index) {
    this.specifications.removeAt(index);
  }

  prizePic(event) {
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        this.prizeImage = event.target.result;
        this.productForm.controls['prizeImage'].setValue(this.prizeImage);
      };
    }
  }

  onSubmit() {
    this.submitted = true;
    if (this.submitted && this.productForm.valid) {
      var data = this.productForm.value;
    this.prizeService.addPrize(data).subscribe(res=>{
      this.toastr.successToastr(res.uiMessage, '', {
        maxShown: 1
    })
      this.router.navigate(['/prize/prize'])
    })
  }
}
}
